<template>

  <div>

    <el-upload
      :class="{disabled: isDisabled}"
      style="margin-left: 4px"
      class="one"
      action=""

      list-type="picture-card"
      :file-list="fileList"
      :on-preview="handlePictureCardPreview"
      :limit="limit"
      :on-remove="handleRemove"
      :http-request="handleUpload"
      :on-change="handleChange"
      :before-upload="handleBeforeUpload"
    >

    </el-upload>
  </div>

</template>

<script>
import COS from 'cos-js-sdk-v5' // (sdk)

const cos = new COS({
  SecretId: 'AKIDfPzJOEo8D3cwc2CAvfFZdRzIKOErHRL0', // ID
  SecretKey: 'VmnY3IpAF4dw3wZw3uVL4cvyoRpzxhDV' // 
})
export default {
  props: {
    // 
    limit: {
      type: Number,
      default: 1
    }
  },
  data () {
    return {
      fileList: [

      ],
      //  /
      dialogVisible: false,
      //  ,
      dialogImageUrl: ''

    }
  },
  computed: {
    // /
    isDisabled () {
      return this.fileList.length >= this.limit
    },
    // (true, false)
    isUploadAll () {
      return this.fileList.every(item => item.status === 'success')
    }
  },
  methods: {
    // 1. 
    handlePictureCardPreview (file) {
      // console.log(file)
      this.dialogImageUrl = file.url
      this.dialogVisible = true
    },
    // 
    handleRemove (file, fileList) {
      console.log(file, fileList, this.fileList)

      this.fileList = [...fileList]
    },
    // 
    handleUpload (obj) {
      // 0. 
      const uid = obj.file.uid
      const file = this.fileList.find(item => item.uid === uid)

      file.status = 'uploading'
      // SDK
      cos.putObject({
        Bucket: 'hrsass-1313596731', /* ,  */
        Region: 'ap-shanghai', /* ,  */
        Key: 'hrsass' + parseInt(Math.random() * 99999) + obj.file.name, /* ,  */
        StorageClass: 'STANDARD',
        Body: obj.file, // 
        onProgress: (progressData) => {
          file.percentage = parseInt(progressData.percent * 100)
        }
      }, (err, data) => {
        if (err) {
          this.$message.error('')
          return
        }
        // 1. statussucess
        // console.log(obj, data)

        setTimeout(() => {
          file.status = 'success'
        }, 1000)
        // 2. url
        file.url = 'https://' + data.Location
      })
    },
    // 
    handleChange (file, fileList) {
      this.fileList = [...fileList]
    },
    // 
    handleBeforeUpload (file) {
      const isLt2M = file.size / 1024 / 1024 <= 2
      if (!isLt2M) {
        this.$message.warning('')
        return false
      }
      return true
    }
  }
}
</script>

<style lang="scss" scoped>

.disabled{
  ::v-deep{
    .el-upload--picture-card {
      display: none;
    }
  }
}

</style>
